/*
  less 变量列表见 Adonis 文档网站 kcore 主题定制 (http://192.168.1.117/kcore/theme)
*/
html,
body {
  overflow: hidden;
}
#app {
  min-height: 100vh;
  position: relative;
}
::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
::-webkit-scrollbar-button {
  display: none;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}
::-webkit-scrollbar-track-piece {
  background: rgba(0, 0, 0, 0.2);
}
.clearfix::before,
.clearfix::after {
  content: '';
  display: table;
}
.clearfix::after {
  clear: both;
}
.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.action-disabled {
  cursor: not-allowed;
  color: #c5c8ce;
}
