
























































































































































































































































































































































































































.OverallData {
  @font-face {
    font-family: 'digifacewide';
    src: url('../../static/fonts/digifacewide.ttf');
  }
  width: 100%;
  height: 100%;
  position: relative;
  background-image: url(../../static/images/yueyang/OverallData-background1.png),
    url(../../static/images/yueyang/OverallData-background2old.png),
    url(../../static/images/yueyang/OverallData-background4.png),
    url(../../static/images/yueyang/OverallData-background3.png),
    url(../../static/images/yueyang/OverallData-background4.png),
    url(../../static/images/yueyang/OverallData-background5.png),
    url(../../static/images/yueyang/OverallData-background.png);
  background-repeat: no-repeat;
  background-position: calc(~'50% - 70px') calc(~'50% - 70px'),
    calc(~'50% - 90px') calc(~'50% + 0px'),
    calc(~'50% + 100px') calc(~'50% + 0px'),
    calc(~'50% - 85px') calc(~'50% + 60px'),
    calc(~'50% + 80px') calc(~'50% + 55px'),
    calc(~'50% + 70px') calc(~'50% - 70px'),
    calc(~'50% + 10px') calc(~'50% - 20px');

  .storeBar {
    height: 100%;
    width: 100%;
    position: absolute;
    .echarts {
      position: absolute;
      top: 42%;
      left: 51%;
      width: 70%;
      height: 70%;
      transform: translate(-50%, -50%);
    }
  }
  .total {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .value {
      color: #ffffff;
      font-size: 14px;
      text-align: center;
    }
    .text {
      color: #f6ff00;
      font-size: 26px;
      text-align: center;
      line-height: 26px;
    }
    .unit {
      color: #f6ff00;
      font-size: 14px;
      text-align: center;
    }
  }
  .textDiv1 {
    top: calc(~'50% - 85px');
    left: calc(~'35% - 100px');
  }
  .textDiv2 {
    bottom: calc(~'50% - 25px');
    left: calc(~'35% - 152px');
    .value {
      padding-left: 10px;
      text-align: left !important;
    }
  }
  .textDiv3 {
    bottom: calc(~'50% - 88px');
    left: calc(~'50% - 176px');
  }
  .textDiv4 {
    bottom: calc(~'50% - 81px');
    right: calc(~'50% - 195px');
  }
  .textDiv5 {
    top: calc(~'50% - 87px');
    right: calc(~'50% - 192px');
  }
  .textDiv6 {
    top: calc(~'50% - 12px');
    right: calc(~'50% - 215px');
  }
  .textDiv7 {
    top: calc(~'50% + 58px');
    right: calc(~'50% - 46px');
  }
  .textDiv {
    position: absolute;
    .value {
      color: #ffffff;
      text-align: center;
      .valueText {
        height: 21px;
        width: 18px;
        display: inline-block;
        font-size: 18px;
        color: #ffffff;
        line-height: 21px;
        text-align: center;
        font-family: 'digifacewide', serif;
        font-weight: bold;
        background-image: url('../../static/images/yueyang/OverallData-text.png');
      }
    }
    .text {
      display: inline-block;
      text-align: center;
      font-size: 8px;
      color: #2776b3;
    }
  }
}
