












































.ChartsBox {
  width: 100%;
  margin-bottom: 10px;
  background: url('../../static/images/yueyang/frame2.png') no-repeat 0 0 / 100%
    100%;
  .house-header-nav {
    display: flex;
    .house-header-nav-text {
      text-align: center;
      font-weight: bold;
      font-size: 14px;
      color: aqua;
      margin-left: 5px;
      margin-right: 5px;
    }
    .imgicon {
      width: 12px;
      height: 10px;
      margin-top: 5px;
    }
  }
  .cont {
    width: 100%;
    height: calc(~'100% - 32px');
  }
}
