// 清除浮动
.clearfix {
  &::before,
  &::after {
    content: '';
    display: table;
  }
  &::after {
    clear: both;
  }
}

// 文本溢出省略
.ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

// 鼠标不可点击样式，右键菜单功能
.action-disabled {
  cursor: not-allowed;
  color: #c5c8ce;
}
