html,
body {
  overflow: hidden;
}

#app {
  min-height: 100vh;
  position: relative;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

::-webkit-scrollbar-button {
  display: none;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

::-webkit-scrollbar-track-piece {
  background: rgba(0, 0, 0, 0.2);
}
